.navbar {
	position: fixed;
	width: 100%;
	z-index: 999;
}

.menu {
	display: flex;
	justify-content: center;
	padding: 0;
	margin: 0;
	&__item {
		margin: 0 0.5rem;
		@media screen and (min-width: $tablet) {
			margin: 0 1rem;
		}
	}
	&__link {
		&:hover {
			color: #ffe4c4;
		}
	}
}
